@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}
html, body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    background-color: white;
    font-family: Poppins, Arial, Helvetica, sans-serif;
}

li, a {
    text-decoration: none;
}

header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    background-color: black;
    border-radius: 25px;
    box-shadow: 0 0px 20px rgba(22, 42, 107, 0.4);
    width: 85%;
    height: auto;
    padding: 12px 5%;
}

.headerContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    padding-top: 1%;
}

.header_title {
    color: whitesmoke;
    font-family: inherit;
    font-size: 1.5rem;
    display: block;
}

.is_active {
    font-weight: bold;
}

.nav_links {
    list-style: none;
}

.nav_links li {
    display: inline-block;
    padding: 0px 12px;
    color: white;
    font-size: 1.0rem;
}

.nav_links li a {
    transition: all 0.3s ease 0s;
    color: white;
}
.nav_links li a:hover {
    color: lightgray;
}
.footwrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    padding-top: 1%;
}

footer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: black;
    color: white;
    width: 100%;
    padding: 3% 5%;
    margin: 6% 4% 0% 4%;
    border-radius: 20px 20px 0px 0px;
}

.byclass {
    font-weight: bold;
}


.wrapper {
	display: -webkit-flex;
	display: flex;
  margin-top: 7%;
	-webkit-justify-content: center;
	        justify-content: center; 
	-webkit-align-items: center; 
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.formContainer {
	display: -webkit-flex;
	display: flex;
  background: white;
	-webkit-justify-content: center;
	        justify-content: center; 
	-webkit-align-items: center; 
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
  border-radius: 25px;
  width: 60%;
  height: 100%;
  min-width: 340px;
  box-shadow: 0 0px 20px rgba(0,0,0,0.4);
  text-align: center;
  padding: 5% 2% 0% 2%;
}

@media only screen and (max-width: 700px) {
  .formContainer {
      width: 80%;
  }    
}

.imageContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
	height: 50%;
	width: 80%;
  padding: 0% 0.5% 2% 0.5%;
}

.imageHolder {
	display: -webkit-flex;
	display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; 
	-webkit-flex-direction: row; 
	        flex-direction: row;
  position: relative;
  top:10%;
  border-radius: 17px;
  margin: 10px;
  border: 2px dashed #c2cdda;
	height: 450px;
	width: 700px;
}

.wrapper img {
  display: none;
  height: 100%;
  width: 100%;
  object-fit: contain;
}

.buttonContainer {
	display: -webkit-flex;
	display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  overflow: hidden;
	width: 100%;
}

.buttonContainer button {
  border: 2px solid #7e9beb;
  outline: none;
  background: #87a1e7;
  position: relative;
  display: absolute;
  margin: 3% auto;
  text-align: center;
  font-size: 1rem; 
  font-weight: bold;
  color: #0d162e;  
  cursor: pointer;
  padding: 2% 4%; /* padding: 12px 50px; */
  border-radius: 24px;
  transition: 0.25s;
}

.buttonContainer button:hover {
  border-color: yellowgreen;
  background: yellowgreen;
}

.wrapper #cancel-btn {
  border: none;
  position: absolute;
  font-size: 20px;
  right: 15px;
  top: 15px;
  color: #84a0ed;
  cursor: pointer;
  display: none;
  background: none;
}

.wrapper #imagespan {
    font-size: 2rem;
    color: #84a0ed;
}
.box {
    width: 60%;
    padding: 50px;
    background: white;
    box-shadow: 0 0px 20px rgba(0,0,0,0.4);
    border-radius: 20px;
    text-align: center;
    margin: 75px auto;
}

.box input {
    border: 2px solid #3498db;
    outline: none;
    display: block;
    margin: 30px auto;
    text-align: left;
    text-indent: 10px;
    font-size: 12px;
    padding: 12px 10px;
    border-radius: 24px;
    width: 70%;
    transition : cubic-bezier(0.175, 0.885, 0.32, 1.275) 200ms;
}

.box input:focus {
    width: 75%;
    border-color: #2eee71;
}

.box button {
    border: 2px solid #2eee71;
    outline: none;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    font-size: 13px; 
    font-weight: bold;   
    cursor: pointer;
    padding: 12px 50px;
    border-radius: 24px;
    transition: 0.25s;
}

.box button:hover {
    background: #2eee71;
}

.box input[type='file'] {
    text-indent: 2px;
}

.box input[type='file']:hover {
    width: 75%;
    border-color: #2eee71;
}

.box div label {
    float: left;
    font-size: 12px;
    margin-left: 16%;
    padding-bottom: 8px;
}

.box .field {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-columns: repeat(var(--repeat, 2), 1fr);
    text-align: center;
}

.box .resultText {
    font-size: 1.1em;
    color: #84a0ed;
    margin: 30px 30px 30px 5px;
}

@media only screen and (max-width: 1000px) {
    .box .field {
        --repeat: 1;
    }
  }
.Predwrapper {
    display: -webkit-flex;
    display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
    -webkit-flex-flow: wrap;
	        flex-flow: wrap;
}

.boxContainer {
    width: 40%;
    padding: 3%;
    background: white;
    box-shadow: 0 0px 20px rgba(0,0,0,0.4);
    border-radius: 17px;
    text-align: center;
    margin: 75px auto;
}

@media only screen and (max-width: 700px) {
    .classContainer {
        --repeat: 1;
    }
    .boxContainer {
        width: 80%;
        margin: 75px auto 10px auto;
    }    
  }
.classContainer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-columns: repeat(var(--repeat, 2), 1fr);
    text-align: left;
}

.boxContainer .resultText {
    font-size: 1.1em;
    color: #84a0ed;
    margin: 30px 30px 30px 5px;
}

